button[data-start] {
  color: #fff;
  height: 50px;
  width: 150px;
  background-color: #33d60a;
  font-size: 16px;
  font-weight: bold;
}

button[data-stop] {
  color: #fff;
  height: 50px;
  width: 150px;
  background-color: #cf0a0a;
  font-size: 16px;
  font-weight: bold;
}

button[data-start]:disabled, button[data-stop]:disabled {
  color: #3d3c3c;
  cursor: default;
  background-color: #c0bcbc;
}

.container-btn {
  height: 100vh;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

